<template>
  <img
    :src="`${folder}/${name.replace(/\s/g, '_').concat('.webp')}`"
    :alt="name"
    :title="name"
    :width="width"
    :height="height"
    loading="lazy"
    @click="onClick()"
  />
</template>

<script>
export default {
  name: "Portrait",
  props: {
    folder: {
      value: String,
      default: "",
      required: true
    },
    name: {
      value: String,
      default: "",
      required: true
    },
    width: {
      value: String,
      default: "auto",
      required: false
    },
    height: {
      value: String,
      default: "auto",
      required: false
    }
  },
  methods: {
    onClick() {
      this.$emit("click", this.name);
    }
  }
};
</script>
